.header {
  background-color: $mid-blue;
}

.navbar-header {
  float: none;

  .navbar-toggle {
    border-color: white;
    margin: 23px 15px 0 0;

    .icon-bar {
      background-color: white;
    }

  }

  .navbar-brand {
    display: block;
    height: auto;

    img {
      display: block;
      height: 90px;
      width: auto;

      @include medium {
        height: 50px;
      }

    }

  }

}

nav {
  background-color: $orange;
  border: none;
  border-radius: 0;
  float: none;

  &.navbar-collapse {

    @include medium {
      margin: 0;
      padding: 0;
    }

  }

  .navbar-nav {

    @include medium {
      margin: 0;
    }

  }

}

.navbar-nav li a {
  color: white;
  font-size: 12px;
  line-height: 50px;
  padding: 0 10px;
  text-decoration: none;
  text-transform: uppercase;
  transition: all .3s ease-in-out;

  @include medium {
    line-height: 30px;
    padding: 0;
  }

  @include large {
    font-size: 10px;
    padding: 0 5px;
  }

  @include extra-large {
    font-size: 11px;
    padding: 0 5px;
  }

  &:hover {
    background-color: transparent;
    color: $light-gray;

    @include medium {
      background-color: white;
      color: $orange;
    }

  }

}
