.page {
  padding: 230px 0 50px 0;

  @include medium {
    padding-top: 120px;
  }

  &__title {
    margin-bottom: 40px;

    h3 {
      color: $orange;
      font-family: 'Caveat', sans-serif;
      font-size: 3em;
      margin: 0;
    }

  }

  &__image {

    @include large {
      margin-bottom: 30px;
    }

  }

  &__content {
    margin-bottom: 40px;

    h4, h5, h6 {
      color: $mid-blue;
      font-family: 'Caveat', sans-serif;
      font-size: 2em;
    }

    ul {
      margin-bottom: 30px;
      padding-left: 25px;

      li {
        color: $mid-gray;

        + li {
          margin-top: 10px;
        }

      }

    }

    table {
      border: 1px solid $mid-gray;
      border-collapse: collapse;

      tr {

        td {
          background-color: $light-gray;
          border: 1px solid $mid-gray;
          font-size: 12px;
          padding: 15px;
          vertical-align: top;

          &.title {
            background-color: $yellow;
            width: 200px;
          }
        }
      }
    }

  }

}