.noticias {
  background-color: $light-gray;
  padding: 50px 0;

  &__title {

    h3 {
      color: $mid-blue;
      font-family: 'Caveat', sans-serif;
      font-size: 3em;
      font-weight: 700;
      margin: 0 0 40px 0;
      text-align: center;
    }

  }

  &__item {
    @include large {

      + div {
        margin-top: 30px;
      }

    }

    &__cont {

      h4 {

        a {
          color: $orange;
          transition: all .3s ease-in-out;

          &:hover {
            color: $yellow;
            text-decoration: none;
          }

        }

      }

    }

  }

}
