#mainCarousel {
  background-color: black;

  .carousel-inner {

    .item {
      background-image: url("../images/give.jpg");
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: cover;
      height: 100vh;
      min-height: 500px;
      padding-bottom: 50px;
      text-align: center;

      &:nth-child(2) {
        background-image: url("../images/slide-02-bg.jpg");
      }

      &:nth-child(3) {
        background-image: url("../images/slide-03-bg.jpg");
      }

      .carousel-content {
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        left: 50%;

        @include medium {
          width: 80%;
        }

        h2 {
          color: white;
          font-size: 4em;
          font-family: 'Caveat', sans-serif;
          font-weight: 700;
          letter-spacing: .05em;
          margin: 0 auto 30px auto;
          max-width: 600px;
          text-align: center;
          text-transform: uppercase;
          width: 100%;

          @include extra-large {
            font-size: 2.75em;
            max-width: auto;
            width: 75%;
          }

          @include large {
            font-size: 2.5em;
            width: auto;
          }

        }

        h3 {
          color: white;
          font-size: 2em;
          font-family: 'Caveat', sans-serif;
          font-weight: 400;
          letter-spacing: .05em;
          margin-top: 30px;
          text-align: center;

          + a {
            margin-top: 30px;
          }

          @include medium {
            width: auto;
          }

        }

        &__btn {
          background-color: $orange;
          border-radius: 5px;
          color: white;
          display: inline-block;
          font-size: 1em;
          font-weight: 400;
          letter-spacing: .05em;
          line-height: 50px;
          padding: 0 15px;
          text-align: center;
          text-transform: uppercase;
          transition: all .3s ease-in-out;

          i {
            margin-right: 10px;
          }

          &:hover {
            background-color: white;
            color: $orange;
            text-decoration: none;
          }

        }

      }

    }

  }

}