.accesos {
  background-color: $mid-blue;
  padding: 50px 0;

  &__title {

    h3 {
      color: white;
      font-family: 'Caveat', sans-serif;
      font-size: 3em;
      font-weight: 700;
      margin: 0 0 40px 0;
      text-align: center;
    }

  }

  &__item {
    @include large {

      + div {
        margin-top: 30px;
      }

    }

    &__cont {

      img {
        display: block;
        margin: 0 auto;
        max-width: 100%;
      }

      h4 {

        a {
          color: $yellow;
          display: block;
          font-size: 14px;
          min-height: 30px;
          text-align: center;
          text-transform: uppercase;
          transition: all .3s ease-in-out;

          &:hover {
            color: white;
            text-decoration: none;
          }

        }

      }

      p {
        color: $light-gray;
        font-size: 12px;
        text-align: center;
      }

    }

  }

}
