.blog {
  padding: 230px 0 50px 0;

  @include medium {
    padding-top: 120px;
  }

  &__title {
    margin-bottom: 40px;

    h3 {
      color: $orange;
      font-family: 'Caveat', sans-serif;
      font-size: 3em;
      margin: 0;
    }

  }

  &__sidebar {

    @include large {
      margin-bottom: 30px;
    }

    h4, h5, h6 {
      color: $mid-blue;
      font-family: 'Caveat', sans-serif;
      font-size: 2em;
    }

    ul {
      list-style-type: none;

      li {
        background-color: $light-gray;

        + li {
          margin-top: 1px;
        }

        a {
          color: $dark-gray;
          display: block;
          padding: 10px;
          transition: all .3s ease-in-out;

          &:hover {
            background-color: rgba(white,.8);
            text-decoration: none;
          }

        }

      }

    }

  }

  &__content {
    margin-bottom: 40px;
    @include clearfix;

    h4 {
      color: $mid-blue;
      font-family: 'Caveat', sans-serif;
      font-size: 2em;
    }

    &__item {
      background-color: $light-gray;
      border-radius: 5px;
      padding: 20px;

      @include clearfix;

      + div {
        margin-top: 10px;
      }

      h5 {
        font-family: 'Caveat', sans-serif;
        font-size: 2em;
        margin: 0;

        a {
          color: $mid-blue;
          display: block;
          transition: all .3s ease-in-out;

          &:hover {
            color: $orange;
            text-decoration: none;
          }

        }

      }

      img {
        float: left;
        margin-right: 20px;
        width: 25%;
      }

      p {
        overflow: hidden;
      }

    }

  }

}