.map {
  background-color: $dark-blue;
  height: 380px;
}

.footer {
  background-color: $dark-gray;
  padding: 30px 0 0 0;
  position: relative;

  &__title {
    margin-bottom: 30px;

    h3 {
      color: white;
      font-size: 16px;
      font-weight: 700;
      margin: 0;
      text-transform: uppercase;
    }

  }

  &__item {
    color: $light-gray;

    + div {
      @include large {
        margin-top: 20px;
      }
    }

    h4 {
      font-size: 14px;
      font-weight: 400;
      margin: 0 0 15px 0;
      text-transform: uppercase;
    }

    &__info {
      list-style-type: none;
      margin-bottom: 20px;

      li {
        font-size: 12px;

        + li {
          margin-top: 5px;
        }

        a {
          color: $yellow;
          font-weight: 400;
        }

      }

    }

    &__social {
      list-style-type: none;
      @include clearfix;

      li {
        float: left;

        + li {
          margin-left: 5px;
        }

        a {
          color: white;
          display: block;
          font-size: 20px;
          line-height: 20px;
          opacity: 1;
          transition: all .25s ease-in-out;
          text-align: center;
          width: 20px;

          &:hover {
            color: $yellow;
            opacity: .8;
          }

        }

      }

    }

  }

  &__copyright {
    background-color: black;
    margin-top: 30px;
    padding: 10px 0;
    text-align: center;

    p {
      color: white;
      font-size: 8px;
      letter-spacing: .2em;
      margin: 0;
      text-transform: uppercase;
    }
  }

}